<template>
	<view id="content">
		<view class="header">
			<image src="/static/ico-back.png" class="icon" @click="Toindex"></image>
			<view class="txt">新建报价申请</view>
			<view class="href" @tap="submit">提交</view>
		</view>
		<view class="tab-box">
			<block v-for="(item,index) in items" :key="index">
				<view class="item" :class="{'on':tabIndex == index}" @click="changeTab(index)">
					{{item}}
				</view>
			</block>
		</view>
		<block v-if="tabIndex == 0" class="cont-box">
			<view class="box-style">
				<view class="title">
					<text class="name">基础信息</text>
					<text class="href">历史报价</text>
				</view>
				<view class="dl-style">
					<view class="dl">
						<view class="dt">报价日期<text class="f-red">*</text></view>
						<view class="dd">
							<picker mode="date" :value="essential.stardate" disabled @change="offerDateChange">
								<view class="txt">{{essential.stardate}}</view>
							</picker>
						</view>
					</view>
					<view class="dl">
						<view class="dt">负责人<text class="f-red">*</text></view>
						<view class="dd">
							<input type="text" class="txt" disabled v-model="array1[0]" />
							<!-- <picker :value="index" :range="array1"  disabled class="txt" >
								<view class="txt">{{array1[0]}}</view>
							</picker> -->
						</view>
					</view>
					<view class="dl">
						<view class="dt">商机号</view>
						<view class="dd">
							<picker :value="index" :range="array1" class="txt" @change="firmChange">
								<view class="txt">{{essential.firm}}</view>
							</picker>
						</view>
					</view>
					<view class="dl">
						<view class="dt">报价备注</view>
						<view class="dd">
							<input type="text" class="txt" placeholder="请输入" v-model="essential.offer_remark" />
						</view>
					</view>
				</view>
			</view>
			<view class="box-style">
				<view class="title">
					<text class="name">客户信息</text>
				</view>
				<view class="dl-style">
					<view class="dl">
						<view class="dt">客户公司名称<text class="f-red">*</text></view>
						<view class="dd">
							<picker :value="index" :range="array1" class="txt" @change="companyChange">
								<view class="txt">{{client.companyName}}</view>
							</picker>
						</view>
					</view>
					<view class="dl">
						<view class="dt">客户类型<text class="f-red">*</text></view>
						<view class="dd">
							<input type="text" class="txt" disabled v-model="array1[0]" />
							<!-- <picker :value="index" :range="array1" class="txt" @change="typeChange">
								<view class="txt">{{client.companytype}}</view>
							</picker> -->
						</view>
					</view>
					<view class="dl">
						<view class="dt">客户行业</view>
						<view class="dd">
							<input type="text" class="txt" disabled v-model="array1[0]" />
						</view>
					</view>
					<view class="dl">
						<view class="dt">所属区域</view>
						<view class="dd">
							<input type="text" class="txt" disabled v-model="array1[0]" />
						</view>
					</view>
					<view class="dl">
						<view class="dt">国家<text class="f-red">*</text></view>
						<view class="dd">
							<input type="text" class="txt" disabled v-model="array1[0]" />
						</view>
					</view>
					<view class="dl">
						<view class="dt">省市区<text class="f-red">*</text></view>
						<view class="dd">
							<input type="text" class="txt" disabled v-model="array1[0]" />
						</view>
					</view>
					<view class="dl">
						<view class="dt">客户详细地址<text class="f-red">*</text></view>
						<view class="dd">
							<input type="text" class="txt" disabled v-model="array1[0]" />
						</view>
					</view>
				</view>
			</view>
			<view class="box-style">
				<view class="title">
					<text class="name">特殊申请</text>
				</view>
				<view class="dl-style">
					<view class="dl">
						<view class="dt">是否融资<text class="f-red">*</text></view>
						<view class="dd">
							<radio-group @change="financing">
								<label class="radio">
									<radio value="r1" checked="true" />否
								</label>
								<label class="radio">
									<radio value="r2" />是
								</label>
							</radio-group>
						</view>
					</view>
					<view class="dl">
						<view class="dt">是否贴息<text class="f-red">*</text></view>
						<view class="dd">
							<radio-group @change="interest">
								<label class="radio">
									<radio value="r1" checked="true" />否
								</label>
								<label class="radio">
									<radio value="r2" />是
								</label>
							</radio-group>
						</view>
					</view>
					<view class="dl">
						<view class="dt">是否有违约金<text class="f-red">*</text></view>
						<view class="dd">
							<radio-group @change="liquidated">
								<label class="radio">
									<radio value="r1" checked="true" />否
								</label>
								<label class="radio">
									<radio value="r2" />是
								</label>
							</radio-group>
						</view>
					</view>
					<view class="dl">
						<textarea placeholder="请输入违约金备注信息" class="txt textarea" v-model="special.remark" />


					</view>
				</view>
			</view>
			<!-- <view class="box-style">
				<view class="title">
					<text class="name">赠送配件明细</text>
					<text class="href">选择配件</text>
				</view>
				<view class="dl-style">
					<view class="dl">
						<view class="dt">配件名称</view>
						<view class="dd">
							<input type="text" class="txt" disabled="" />
						</view>
					</view>
					<view class="dl">
						<view class="dt">配件编码</view>
						<view class="dd">
							<input type="text" class="txt" disabled="" />
						</view>
					</view>
					<view class="dl">
						<view class="dt">销售价格</view>
						<view class="dd">
							<input type="text" class="txt" disabled="" />
						</view>
					</view>
					<view class="dl">
						<view class="dt">数量</view>
						<view class="dd">
							<input type="text" class="txt" disabled="" />
						</view>
					</view>
					<view class="dl">
						<view class="dt">金额</view>
						<view class="dd">
							<input type="text" class="txt" disabled v-model="array1[0]" />
						</view>
					</view>
				</view>
			</view> -->
			<view class="box-style">
				<view class="dl-style">
					<view class="dl">
						<view class="dt">成交方式（海外）<text class="f-red">*</text></view>
						<view class="dd">
							<picker :value="index" :range="array1" class="txt" @change="makeChange">
								<view class="txt">{{rest.maketype}}</view>
							</picker>
						</view>
					</view>
					<view class="dl">
						<view class="dt">纠纷解决争议地</view>
						<view class="dd">
							<picker :value="index" :range="array1" class="txt" @change="disputeChange">
								<view class="txt">{{rest.dispute}}</view>
							</picker>
						</view>
					</view>
					<view class="dl">
						<view class="dt">销售组织</view>
						<view class="dd">
							<input type="text" class="txt" disabled v-model="array1[0]" />
							<!-- <picker :value="index" :range="array1" class="txt" @change="organizationChange">
								<view class="txt">{{array1[index]}}</view>
							</picker> -->
						</view>
					</view>
					<view class="dl">
						<view class="dt">包装方式</view>
						<view class="dd">
							<picker :value="index" :range="array1" class="txt" @change="packagingChange">
								<view class="txt">{{rest.packaging}}</view>
							</picker>
						</view>
					</view>
					<view class="dl">
						<view class="dt">渠道代理费</view>
						<view class="dd">
							<picker :value="index" :range="array1" class="txt" @change="channelChange">
								<view class="txt">{{rest.channel}}</view>
							</picker>
						</view>
					</view>
					<view class="dl">
						<view class="dt">交期期限</view>
						<view class="dd">
							<input type="text" class="txt" placeholder="请输入" v-model="rest.delivery" />
						</view>
					</view>
					<view class="dl">
						<view class="dt">运费承担方</view>
						<view class="dd">
							<picker :value="index" :range="array1" class="txt" @change="bearerChange">
								<view class="txt">{{rest.bearer}}</view>
							</picker>
						</view>
					</view>
					<view class="dl">
						<view class="dt">赠送配件金额</view>
						<view class="dd">
							<input type="text" class="txt" placeholder="请输入" v-model="rest.present" />
						</view>
					</view>
				</view>
			</view>

			<view class="box-style">
				<view class="title">
					<text class="name">付款方式</text>
					<text class="href" @click="addNPER()">添加</text>
				</view>
				<view class="addInfo-box">
					<block v-for="(item,inde) in nperitems" :keys="inde">
						<view class="item">
							<view class="dl-style">
								<view class="dl">
									<view class="dt">序号</view>
									<view class="dd">
										<text>{{item.id}}</text>
									</view>
								</view>
								<view class="dl">
									<view class="dt">付款方式</view>
									<view class="dd">
										<picker :value="index" :range="array1" class="txt" @change="detailChange($event,inde)" >
											<view class="txt">{{array1[index]}}</view>
										</picker>
									</view>
								</view>
								<view class="dl">
									<view class="dt">付款类型</view>
									<view class="dd">
										<picker :value="index" :range="array1" class="txt" @change="wayChange">
											<view class="txt">{{array1[index]}}</view>
										</picker>
									</view>
								</view>
								<view class="dl">
									<view class="dt">付款占比</view>
									<view class="dd">
										<input type="text" class="txt" disabled placeholder="请输入" />
									</view>
								</view>
								<view class="dl">
									<view class="dt">付款金额</view>
									<view class="dd">
										<input type="text" class="txt" placeholder="请输入" />
									</view>
								</view>
							</view>
							<view class="del" @click="deleteMessage()" v-if="deleteShowNum == item.id">
								<image src="/static/ico-del.png" class="ico"></image>删除信息
							</view>
						</view>
					</block>
				</view>
			</view>
		</block>
		<block v-if="tabIndex == 1" class="cont-box">
			<view class="box-style">
				<view class="title">
					<text class="name">产品信息</text>
					<text class="href" @click="visiblePro = true">添加</text>
				</view>
				<view class="pro-wrap">
					<view class="info-box">
						<image src="/static/test002.png" class="img"></image>
						<view class="name">TM65 X900 3KW专业切管激光切割机整机</view>
						<view>申请报价：<text class="f-blue">￥86000</text></view>
						<view>原价：￥90000</view>
						<view>数量：1</view>
					</view>
					<view class="price-box">
						<view class="item"><text>销售指导价：</text>￥1999</view>
						<view class="item"><text>标机成本：</text>￥1999</view>
						<view class="item"><text>选配浮动成本：</text>￥1999</view>
						<view class="item"><text>总成本：</text>￥1999</view>
					</view>
					<!-- <view class="btns-box">
						<button class="btn">编辑</button>
					</view> -->
				</view>
			</view>

			<view class="box-style">
				<view class="title">
					<text class="name">配件信息</text>
					<text class="href" @click="visibleParts = true">添加</text>
				</view>
				<view class="addInfo-box">
					<block v-for="(item,index) in parts" :keys="index">
						<view class="item">
							<view class="dl-style">
								<view class="dl">
									<view class="dt">产品名称</view>
									<view class="dd arrow">
										<input type="text" class="txt" disabled="" value="TMAIN 下料机" />
									</view>
								</view>
								<view class="dl">
									<view class="dt">单位</view>
									<view class="dd">
										<input type="text" class="txt" placeholder="PCS" />
									</view>
								</view>
								<view class="dl">
									<view class="dt">数量</view>
									<view class="dd">
										<input type="text" class="txt" value="1" />
									</view>
								</view>
								<view class="dl">
									<view class="dt">销售面额</view>
									<view class="dd">
										<input type="text" class="txt" value="199" />
									</view>
								</view>
								<view class="dl">
									<view class="dt">报价金额</view>
									<view class="dd">
										<input type="text" class="txt" value="108" />
									</view>
								</view>
							</view>
							<view class="del" @tap="configurationDelete">
								<image src="/static/ico-del.png" class="ico"></image>删除信息
							</view>
						</view>
					</block>
				</view>
			</view>

			<view class="box-style">
				<view class="title">
					<text class="name">特殊申请</text>
				</view>
				<view class="dl-style">
					<view class="dl">
						<view class="dt">赠送移机次数</view>
						<view class="dd">
							<view class="nums-box">
								<button class="n-b" @tap="specialSub('presenter')">-</button>
								<input type="number" class="n-t" value="0" v-model="special.presenter">
								<button class="n-b" @tap="specialAdd('presenter')">+</button>
							</view>
						</view>
					</view>
					<view class="dl">
						<view class="dt">移机服务费用</view>
						<view class="dd">
							<input type="text" class="txt" disabled="" />
						</view>
					</view>
					<view class="dl">
						<view class="dt">激光器延保（年）</view>
						<view class="dd">
							<view class="nums-box">
								<button class="n-b" @tap="specialSub('warranty')">-</button>
								<input type="number" class="n-t" value="0" v-model="special.warranty">
								<button class="n-b" @tap="specialAdd('warranty')">+</button>
							</view>
						</view>
					</view>
					<view class="dl">
						<view class="dt">激光器延保费用</view>
						<view class="dd">
							<input type="text" class="txt" disabled="" />
						</view>
					</view>
					<view class="dl">
						<view class="dt">机床延保（年）</view>
						<view class="dd">
							<view class="nums-box">
								<button class="n-b" @tap="specialSub('tool')">-</button>
								<input type="number" class="n-t" value="0" v-model="special.tool">
								<button class="n-b" @tap="specialAdd('tool')">+</button>
							</view>
						</view>
					</view>
					<view class="dl">
						<view class="dt">机床延保费用</view>
						<view class="dd">
							<input type="text" class="txt" disabled="" />
						</view>
					</view>
					<view class="dl">
						<view class="dt">赠送耗材金额</view>
						<view class="dd">
							<input type="text" class="txt" placeholder="请输入" v-model="special.consumable" />
						</view>
					</view>
					<view class="dl">
						<view class="dt">建议交期</view>
						<view class="dd">
							<input type="text" class="txt" placeholder="请输入" v-model="special.suggest" />
						</view>
					</view>
				</view>
			</view>
			<view class="box-style">
				<view class="dl-style">
					<view class="dl">
						<textarea class="txt textarea" placeholder="请输入备注信息" v-model="special.remark"></textarea>
					</view>
					<view class="upload-box">
						<view class="tit">特殊情况提前申请截图</view>
						<view class="a-upload">
							<image src="/static/ico-upload.png" @click="upload"></image>
						</view>
						<view class="upload-list">
							<view class="item">
								<image src="/static/test.png"></image>
								<view @tap="deleteimg" class="close"></view>
							</view>
						</view>
					</view>
				</view>
			</view>
		</block>
		<view class="pup-box" v-if="visibleParts">
			<view class="header">
				<view @click="visibleParts = false" class="icon">
					<image src="/static/ico-back.png"></image>
				</view>
				<view class="txt">添加配件</view>
			</view>
			<view class="tab-box">
				<block v-for="(item,index) in partsTab" :key="index">
					<view class="item" :class="{'on':partstabIndex == index}" @click="changeTab1(index)">
						{{item}}
					</view>
				</block>
			</view>
			<view class="search-medium">
				<image src="/static/ico-search1.png" class="ico"></image>
				<input type="search" placeholder="产品名称/产品编号" class="txt" @tap="burdeningSearch"
					@confirm="burdeningSearch" />
			</view>
			<block v-if="partstabIndex == 0">
				<view class="parts-list">
					<view class="item">
						<view class="name">TXAL01-12全自动上料机</view>
						<view><text>功率：</text>2000w</view>
						<view><text>产品编码：</text>0508088025</view>
						<!-- <button class="add-btn" @click="accessoriesAdd"></button> -->
						<view class="nums-box">
							<button class="n-b" @tap="specialSub('tool')">-</button>
							<input type="number" class="n-t" value="0" v-model="special.tool">
							<button class="n-b" @tap="specialAdd('tool')">+</button>
						</view>
					</view>
				</view>
			</block>
			<!-- 添加上下料 -->
			<block v-if="partstabIndex == 1">
				<view class="parts-list">
					<view class="item">
						<view class="name">TXAL01-12全自动上料机</view>
						<view><text>规格：</text>2000w</view>
						<view><text>产品编码：</text>0508088025</view>
						<!-- <button class="add-btn" @tap="materialAdd"></button> -->
						<view class="nums-box">
							<button class="n-b" @tap="specialSub('tool')">-</button>
							<input type="number" class="n-t" value="0" v-model="special.tool">
							<button class="n-b" @tap="specialAdd('tool')">+</button>
						</view>
					</view>
				</view>
			</block>
			<block v-if="selectedPro == 1">
				<view class="pup-mask"></view>
				<view class="select-pro">
					<view class="tit">已选产品</view>
					<view class="parts-list">
						<view class="item">
							<view class="name">TXAL01-12全自动上料机</view>
							<view><text>功率：</text>2000w</view>
							<button class="close-btn" @tap="accessoriesDelete"></button>
						</view>
					</view>
				</view>
			</block>
			<view class="operation-box">
				<view class="txt" :class="{'on':selectedPro == 1}" @click="showpro()">
					已选产品：<text class="f-blue arrow">4</text>
				</view>
				<view class="btns">
					<button class="btn" @click="accessoriesReset">重置</button>
					<button class="btn btn-blue" @tap="accessoriesConfirm">确定</button>
				</view>
			</view>
		</view>
		<view class="pup-box" v-if="visiblePro">
			<view class="header">
				<view @click="visiblePro = false" class="icon">
					<image src="/static/ico-back.png"></image>
				</view>
				<view class="txt">添加报价单明细</view>
			</view>
			<view class="search-medium">
				<image src="/static/ico-search1.png" class="ico"></image>
				<input type="search" placeholder="产品名称/产品编号" class="txt" @tap="detailSearch" @confirm="detailSearch" />
			</view>
			<view class="quotation-box">
				<block v-for="(item,index) in quotationArray" :key="index">
					<view class="item">
						<view class="title" :class="{'on':item.quoVisible}" @click="showquoInfo(item,index)">
							{{item.name}}
						</view>
						<view class="quotation-list" v-if="item.quoVisible">
							<view class="item">
								<image src="/static/test002.png" class="img"></image>
								<view class="name"><text class="tag">GH</text>30KW总线激光切割器</view>
								<view><text>功率：</text>2000w</view>
								<view><text>产品铭牌：</text>CE</view>
								<view><text>销售面价：</text>￥360,000.00</view>
								<view class="nums-box">
									<button class="n-b">-</button>
									<input type="number" class="n-t" value="0">
									<button class="n-b">+</button>
								</view>
							</view>
						</view>
					</view>
				</block>
			</view>
			<block v-if="selectedPro == 1">
				<view class="pup-mask"></view>
				<view class="select-pro">
					<view class="tit">已选产品</view>
