				if (type == 'presenter') {
					if (this.special.presenter <= 0) {
						this.remind('不能再少了')
					} else {
						this.special.presenter--
					}
					return
				}
				if (type == 'warranty') {
					if (this.special.warranty <= 0) {
						this.remind('不能再少了')
					} else {
						this.special.warranty--
					}
					return
				}
				if (type == 'tool') {
					if (this.special.tool <= 0) {
						this.remind('不能再少了')
					} else {
						this.special.tool--
					}
					return
				}
